:root {
  text-align: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.category{
  display: flex;
  width: 80%; 
  margin: auto;
  padding: 20px;
  justify-content: space-around;
  /* margin: 0 20px 0 20px; */
}
.category button{
  padding: 5px;
  color: red;
  background-color: white;
  border: 1px solid #a8a2a2;
  border-radius: 5px;
  font-weight: 600;
  cursor: pointer;
}
.category button:hover{
  background-color: red;
  color: white;
  transition: 1s;
}
.product::-webkit-scrollbar {
  display: none;
}

.product {
  overflow:auto ;
  height: 90vh;
  width: min(100%,1500px);
}

.search {
  border: 1px solid red;
  width: 20%;
  display: grid;
  grid-template-columns: 2fr 0.8fr;
  align-items: center;
  justify-content: space-between;
  margin: auto;
  margin-top: 50px;
  font-size: 1rem;
  padding: 8px;
}

::placeholder {
  color: red;
}

.searchbox {
  color: red;
  background-color: white;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 5px;
  border: 1px solid #a8a2a2;
  width: 20%;
  display: grid;
  grid-template-columns: 2fr 0.8fr;
  align-items: center;
  justify-content: space-between;
  margin: auto;
  margin-top: 50px;
  font-size: 1rem;
  padding: 8px;
}
.productlist {
  justify-content: center;
  margin-top: 40px;
  width: 80%;
  margin: auto;
} 
.carts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  list-style: none;
  
}
.cart {
  display: grid;
  height: 330px;
  grid-template-rows: 3fr 2fr 1fr;
  text-align: center;
  border: 1px solid rgba(221, 211, 211, 0.767);
  margin: 0px 10px 20px 10px;
  backdrop-filter: blur(15px);
  background: transparent;
}
.cart-image{
  height: 220px;
}
.cart-image img{
  height: 100%;
  width: 100%;
}
.cart-text{
  padding: 5px;
  display: grid;
  grid-template-columns: 1fr 0.3fr;
  justify-content: center;
  align-items: center;
  text-align: start;
}
.cart-text h4{
  margin: 5px 0 5px 0;
}
.reating {
  font-weight: 400;
  font-size: 20px;
  color:red;
  padding: 2px;
  background:transparent  ;
  border: 1px solid #a8a2a2;
}
.reating:hover {
  color: white;
  background-color: red;
  transition: 1s;
}
.cart-button {
  width: 90%;
  padding: 5px;
  cursor: pointer;
  font-weight: 600;
  color: red;
  background-color: white;
  border: 1px solid #a8a2a2;
  transition: 1s;
}
.cart-button:hover{
  color: white;
  background-color: red;
}

.buttons {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.price {
  margin-top: 8px;
  margin-bottom: 8px;
  >h5 {
    margin-top: 2px;
    padding-left: 8px;
    text-decoration: line-through;
  }
}

.opencart {
  background-color: #c9b4ccbb;
  backdrop-filter: blur(15px);
  position: absolute;
  font-size: 1.5rem;
  margin-left: 20px;
  font-weight: 700;
  top: 5rem;
  left: 0.5rem;
  cursor: pointer;
}