*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
 }
  .nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    overflow: auto;
    /* border: 1px rgb(255, 0, 0) solid; */
    color: rgb(255, 0, 0);
    /* background-color: rgb(245, 241, 241); */
    background-color: rgb(63, 52, 52);
    width: 100% ;
}
.nav-heading{ 
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
    width: 40%;
    /* border: 1px black solid; */
    margin: auto;
    text-align: start;


 }
 .nav-heading h2{
   margin-left: 5px;
 }
  .nav-link{
    display: flex;
    /* border: 1px black solid; */
    list-style: none;
    justify-content: space-evenly;
    width: 50% ;
    color: rgb(255, 255, 255);
  }
 .navinput{
  margin-left: 5px;
  font-size: 16px;
   border:none;
   background: transparent;
   width: 30px;
   color: red;
   font-weight: 600;
   /* background-color: non; */
 }
  .home,.products,.navcart,.login{
    padding: 5px;
    margin: 20px;
    font-size: 25px;
    cursor: pointer;
    color: rgb(255, 0, 0);
    text-decoration: none;
    font-size: 25px;
    border: none;
    background: none;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
    transition: 5ms;

  }
  .menu{
    display: none;
    font-size: 30px;
    font-weight: bold;
    list-style: none;
  }

 
  @media  screen and (max-width:600px) {
        .menu{
          display: flex;
          margin-right: 20px;
          color: white;
          background-color:red;
                  
        }    
        .nav-link{
         display: none;
        }
       .mobilenav-link{
         position: absolute;
         text-align: center;
         /* margin-top: -10px; */
         align-items: center;
         display: grid;
         justify-content: center;
         padding: 10px;
         list-style:none ;
         border-radius: 3%;
         background: transparent;
         backdrop-filter: blur(14px);
         width: 50%;
         height: max-content;
         top: 85px;
         right: 35px;
         border: solid 1px red ;
         z-index: 2;
        }
      .mobilenav-link li{
         margin: 40px;
   
        }
