  .home{
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 600;
  }
  .home:hover {
    border-bottom: 2px solid red;
    }
  .products {
    font-size: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;
    }
  .products:hover {
