.addcart-page{
    display: grid;
    grid-template-columns: 4fr 1fr;
    height: 90vh;
}
.addcart-sidebar{
    background-color: rgb(245, 241, 241);

}
.newcarts{
    overflow: auto;
    padding: 15px;
}
.newcarts h2{
    text-align: start;
    color: red;
}
.newcart{
    justify-content: space-around;
    border: 1px red solid;
    margin: 15px;
    display: grid;
    grid-template-columns: 1fr 1fr 0.5fr 0.5fr 1fr;
    text-align: left;
    align-items: center;    
    border-radius: 5px;
    padding: 5px;
    >h5{
       text-decoration: line-through;
       font: lighter;    
    }
}
.newcart button{
    padding: 3px 3px 3px 3px;
    bottom: 0;
    color: red;
    background-color: white;
    /* border: none; */
    border: 1px solid #a8a2a2;
    cursor: pointer;
    border-radius: 10%;
    font-weight: 900; 
    width: 20%;
    margin: auto;   
    transition: 1s;
}
.newcart button:hover{
    color: white;
    background-color: red;
  }
.total-box{
    background-color: white;
    margin:20px ;
    text-align: center;
    height: fit-content;
    padding: 5px;
}
.total-box h4{
    text-align: start;
    padding: 10px;
    font-weight: 200;
    margin: 2px;
    font-size: 20px;
}
.total-box h4 input{
    border: none;
    font-weight: 500;
    font-size: 17px;
    padding: 1px;

    width: 20%;
}
.total-box p{
   color: rgb(31, 177, 56);
   padding: 10px;
   text-align: start;

}
.total-box button{
    padding: 3px 8px 3px 8px;
    bottom: 0;
    color: red;
    background-color: white;
    /* border: none; */
    border: 1px solid #a8a2a2;
    cursor: pointer;
    border-radius: 10%;
    font-weight: 700;
    /* font-size: 15px; */

    /* text-align: center; */
    /* align-items:cen; */
}
.total-box button:hover{
    color: white;
    background-color: red;
}
.newimg{
    height: 50px;
    width: 50px;
}
@media screen and (max-width: 600px) {
    .addcart-page{
        display: flex;
        flex-direction: column;
        /* grid-template-rows: 3fr 2fr ; */
        /* height: 90vh; */
        border: 1px red solid;
    }  
    .addcart-sidebar{
        height: 30vh;    
    }
    .newcarts{
       height: 60vh;
    }
}
