.login-app{
    /* position: fixed; */
    height: 91vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.login-page{
    background: transparent;
    background-color: rgba(210, 42, 216, 0.178);
    backdrop-filter: blur(25px);
    min-width:400px;
    border-radius: 0.4rem;
    box-shadow: 0 1px 10px #999;
    min-height: 400px;
    display: flex;
    flex-direction: column;
    /* position: relative; */
    border: solid black;
}
.login-title{
    font-size: 30px;
    padding: 1rem;
    border-bottom: 1px solid black;
}
.login-con{
    text-align: start;
    margin-top: 20px;
    font-size: 20px;
    margin-left: 110px;
}

.login-input {
  background-color: #212121;
  max-width: 190px;
  height: 40px;
  padding: 10px;
  /* text-align: center; */
  color: rgb(0, 255, 255);
  border: 2px solid white;
  border-radius: 5px;
  /* box-shadow: 3px 3px 2px rgb(249, 255, 85); */
}

.login-input:focus {
  color: rgb(0, 255, 255);
  background-color: #212121;
  outline-color: rgb(0, 255, 255);
  box-shadow: -3px -3px 15px rgb(0, 255, 255);
  transition: .1s;
  transition-property: box-shadow;
}

