.categorypage-carts{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    list-style: none;
}
.categorypage-cart{
    display: grid;
    height: 330px;
    grid-template-rows: 3fr 2fr 1fr;
    text-align: center;
    border: 1px solid rgba(221, 211, 211, 0.767);
    margin: 0px 10px 20px 10px;
    backdrop-filter: blur(15px);
    background: transparent;
}