  .homepage {
    display: grid;
    justify-content: center;
    color: white;
    z-index: 1;
    height: 80vh;
    width: 98%;
    overflow: hidden;
    position: relative; /* Necessary for absolute positioning of the video */
    margin: auto;
    margin-top: 1%;
  }
  
  .homepage video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;

  }
  
  .hometext{
    width: 60%;
    height: 100%;
    margin-bottom: 20px;
    font-size: 20px;
    margin: auto;
  }
  .hometext p{
    margin-bottom: 20px;
  }
  .homepage-btn{
    margin-top: 15px;
    padding: 5px;
    font-weight: 600;
    cursor: pointer;
    border: solid 1px rgb(211, 199, 199);
    color: rgb(39, 37, 37);
    background-color: white;
    color: red;
    /* background: transparent; */
    /* background-color:white; */
    text-decoration: none;
    transition: 1s;
  }
  .homepage-btn:hover{
    color: white;
    background-color: red;
  }
  .homepageto-menu{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 60vh;
    background-color: white;
    /* border: solid 1px rgb(255, 0, 0); */
    width: 80%;
    margin: auto;
    margin-top: 25px;
    align-items: center;
  }
  
  .homepageto-menutext p{
    margin-bottom: 10px; 
    font-weight: 600;
    color: red;
  }
  .homepageto-menuimage{
    height: 100%;
    width: 100%;
  }
  .homepageto-menuimage img{
    height: 100%;
    width: 100%;
  }
