.loginpage-app{
    display: flex;
    height: 90vh;
    width: 100%;
    /* border: solid  black; */
    max-height: 92vh;
     align-items: center; 
    justify-content: center; 
    background-image: url(images/home.jpg);
    background-size: cover;
    background-position: center ;
    background-repeat: no-repeat;
}
.loginpage-full{ 
  border: 1px solid rgba(221, 211, 211, 0.767);
   display: grid;
   grid-template-rows: 0.5fr 1fr  0fr 1fr   ;
  border-radius: 0.4rem;
    box-shadow: 0 1px 10px #999;
    min-height: 350px;
    min-width: 350px;
    background: transparent;
    /* filter: blur(15px); */
    backdrop-filter: blur(15px);
  }

  .loginpage-taitel{
    font-size: 20px;
    padding: 1rem;
    border: 1px solid rgba(221, 211, 211, 0.767);
  }

.loginpage-con{
    /* text-align: start; */
    margin-top: 35px;
    font-size: 20px;
   /* margin-left: 110px; */
}

