.Adminhome-page{
    border: 1px red solid;
    width: 100%;
    align-items: center;
    justify-content: center;
    height: 90vh;
}
.Adminhome-page h3{
    color: red;
    margin: 10px 0 10px 0; 
  }
.Adminhome-links{
    display: flex;
}
.Adminhome-link{
    padding: 5px;
    color: red;
    background-color: white;
    border: 1px solid #a8a2a2;
    border-radius: 5px;
    font-weight: 600;
    cursor: pointer;
    text-decoration: none;
    width: 25%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 2px;
}
.Adminhome{
    margin: auto;
    font-size: auto;
    padding: 10px;
    margin-top: 50px;    
}
.Adminhome-inputs{
    /* background-color: brown; */
    margin: 2px;
    text-align: start;
    width: 75%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin-top: 20px;
    margin-bottom:20px;
}
.Adminhome-inputs label{
   padding: 5px;
   color: red;
   font-weight: 600;

}
.Adminhome-inputs input{
    /* border: red 1px solid; */
   padding: 5px;
}
.Adminhome-btn{
    width: 30%;
    padding: 5px;
    color: red;
    background-color: white;
    border: 1px solid #a8a2a2;
    border-radius: 5px;
    font-weight: 600;
    cursor: pointer;
    margin-top: 10px;
}