.details{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.de_detail{
    backdrop-filter: blur(15px);
    background-color: #8b1196c4;
    max-width: 600px;
    border-radius: 0.4rem;
    box-shadow: 0 1px 10px #999;
    max-height: 600px;
    display: flex;
    flex-direction: column;
    position: relative;
}

.de_title{
    padding: 1rem;
    border-bottom: 1px solid black;
}
.deimg{
    filter: drop-shadow(10px 10px 10px  );
  }
.de_content{
    flex: 1;

}
.decart{
    list-style: none;
    /* padding: 5px; */
    /* >h5{
      text-align: start;
    } */
}

.incard{
    padding: 10px;
    display: flex;
    margin: 10px;
}
.cartright{
    padding: 1px;
    >h6{
        text-decoration: line-through;
        margin-top: 5px;
    }
}
.deimg{
    height: 150px;
    width: 150px;
}
.deimg:hover{
    transform: rotatex(360deg);
    transition: 1s;
  }
.cartleft{
    margin-left:35px; 
    padding: 10px;
    text-align: left;
}

.de_footer{
    padding: 10px;
    border-top: 1px solid black;
    display: flex;
    justify-content: space-between;
}
.detailli{
    padding: 5px;
}

.de_close{
    position: absolute;
    font-size: 2rem;
    font-weight: bold;
    top:0.2rem;
    right: 1rem;
    cursor: pointer;

}
