.btn {
    width: 5em;
    position: relative;
    height: 2.5em;
    border: 3px ridge #149CEA;
    outline: none;
    background-color: transparent;
    color: rgb(0, 0, 0);
    transition: 1s;
    border-radius: 0.3em;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    margin-top: 5px;
  }
  
  .btn::after {
    content: "";
    position: absolute;
    top: 0px;
    left: 3%;
    width: 95%;
    height: 4%;
    background-color: #212121;
    transition: 0.5s;
    transform-origin: center;
  }
  
  .btn::before {
    content: "";
    transform-origin: center;
    position: absolute;
    top: 100%;
    left: 3%;
    width: 95%;
    height: 4%;
    background-color: #212121;
    transition: 0.5s;
  }
  
  .btn:hover::before, button:hover::after {
    transform: scale(0)
  }
  
  .btn:hover {
    box-shadow: inset 0px 0px 25px #060f1a;
  }
  .numbtn {
    width: 2.5em;
    position: relative;
    height: 2em;
    border: 3px ridge #149CEA;
    outline: none;
    background-color: transparent;
    color: rgb(0, 0, 0);
    transition: 1s;
    border-radius: 0.3em;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    margin-top: 5px;
  }
  
  .numbtn::after {
    content: "";
    position: absolute;
    top: 0px;
    left: 3%;
    width: 95%;
    height: 4%;
    background-color: #212121;
    transition: 0.5s;
    transform-origin: center;
  }
  
  .numbtn::before {
    content: "";
    transform-origin: center;
    position: absolute;
    top: 100%;
    left: 3%;
    width: 95%;
    height: 4%;
    background-color: #212121;
    transition: 0.5s;
  }
  
  .numbtn:hover::before, button:hover::after {
    transform: scale(0)
  }
  
  .numbtn:hover {
    box-shadow: inset 0px 0px 25px #060f1a;
  }