.categoryfullpage{

}
.categoryfullpage h2 {
    color: red;
    margin-top: 25px;
}
.categorysitam{
    width: 80%;
    border: 1px solid red;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    /* grid-template-columns:  ; */
    /* width: 80%;  */
    margin: auto;
    margin-top: 25px;
    padding: 10px;
    justify-content: space-around;
}
.category-menu{
    display: grid;
    grid-template-rows: 4fr 0.5fr;
    border: 1px solid rgb(0, 0, 0);
    margin: 2px;
    padding: 0%;
  
}
.category-menu img{
    padding: 0;
    width: 100%;
    background-color: aquamarine;
    margin: 0;
}
